
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import InfiniteLoading from 'vue-infinite-loading'
import InlineLoading from '@/components/InlineLoading'
import { isInteger, htmlDecode } from '@/utils/utils'
import { addSong, getSongs, manualAddSong, getShelvesAmount, getMerchantSetting, merchantSetting } from '@/api/'
export default {
directives: {
TransferDom
},
components: {
Checker,
CheckerItem,
CheckIcon,
Search,
GroupTitle,
Group,
Cell,
XInput,
XSwitch,
XButton,
Datetime,
XDialog,
InfiniteLoading,
InlineLoading,
BpSongs: () => import('@/components/BapingSetting/BapingSongs')
},
data () {
return {
resultHeight: 0,
focus: false,
value: '',
form: {
ht_id: this.$route.query.id,
amount: 50,
open_hour: 1,
price: 100,
open_time: '',
is_shelves: '2',
is_open: '2'
},
manualForm: {
name: '',
author: ''
},
requestParams: {
page: 1,
pageSize: 20
},
songList: [],
songListValue: [],
customVisible: false,
songsVisible: false,
songNum: 0
}
},
beforeRouteEnter (to, from, next) {
document.title = ''
next()
},
beforeRouteLeave (to, from, next) {
window.jsonp4 = null
next()
},
created () {
this.getSongNumber()
getMerchantSetting({ht_id: this.$route.query.id}).then((res) => {
if (res.result) {
res.result.price = Number(res.result.price)
this.form = Object.assign(this.form, res.result)
}
})
},
mounted () {
this.resultHeight = window.innerHeight - 44
window.jsonp4 = (response) => {
getSongs({ht_id: this.$route.query.id}).then((res) => {
if (Array.isArray(res.result)) {
res.result.map(v => {
this.songListValue.push(Number(v.song_id))
})
}
let { song } = response.data
song.list.forEach((v) => {
v.songname = htmlDecode(v.songname)
v.author = Array.isArray(v.singer) && v.singer.length > 0 ? htmlDecode(v.singer[0].name) : ''
})
this.songList = [...this.songList, ...song.list]
if (this.songList.length < song.totalnum) {
this.requestParams.page++
this.infiniteLoading && this.infiniteLoading.loaded()
} else {
if (this.infiniteLoading && song.totalnum !== 0) {
this.infiniteLoading.loaded()
this.infiniteLoading.complete()
} else {
this.infiniteLoading.complete()
}
}
}).catch(() => {
this.infiniteLoading.complete()
})
}
},
methods: {
showDate () {
let _self = this
document.body.classList.add('third-center')
this.$vux.datetime.show({
cancelText: '',
confirmText: '',
format: 'HH:mm',
value: this.form.open_time,
onConfirm (val) {
_self.form.open_time = val
},
onHide () {
setTimeout(() => {
document.body.classList.remove('third-center')
}, 350)
}
})
},
saveSetting () {
merchantSetting(this.form).then((res) => {
this.$vux.toast.show('')
})
},
getSongNumber () {
getShelvesAmount({ht_id: this.$route.query.id}).then((res) => {
this.songNum = res.result
})
},
onClick (itemValue, itemDisabled) {
let song = this.songList.find(v => v.songid === itemValue)
if (song) {
let params = {
ht_id: this.$route.query.id,
song_id: song.songid,
name: song.songname,
author: song.author
}
addSong(params).then(() => {
this.getSongNumber()
})
}
},
clearForm () {
Object.keys(this.manualForm).forEach(v => {
this.manualForm[v] = ''
})
},
validisPrice (value) {
if (!isInteger(value)) {
this.form.price = 1
} else if (value > 9999) {
this.form.price = 9999
console.log(this.form.price)
}
},
validisTime (value) {
if (!isInteger(value)) {
this.form.open_hour = 1
} else if (value > 24) {
this.form.open_hour = 24
}
},
validisSong (value) {
if (!isInteger(value)) {
this.form.amount = 1
} else if (value > 999) {
this.form.amount = 999
}
this.$forceUpdate()
},
onFocus () {
this.focus = true
this.$noscroll()
},
onBlur () {
this.$noscroll.cancel()
this.focus = false
},
onCancel () {
this.value = ''
this.songList = []
this.songListValue = []
},
onSubmit () {
this.songList = []
this.songListValue = []
this.requestParams.page = 1
this.infiniteLoading && this.infiniteLoading.reset()
this.getResult()
},
getResult () {
if (!this.infiniteLoading) {
this.$refs.infiniteLoading.attemptLoad()
} else {
this.$jsonp('http://i.y.qq.com/s.music/fcgi-bin/search_for_qq_cp', {
g_tk: 938407465,
uin: 0,
format: 'jsonp',
inCharset: 'utf-8',
outCharset: 'utf-8',
notice: 0,
platform: 'h5',
needNewCode: 1,
w: this.value,
zhidaqu: 1,
catZhida: 1,
t: 0,
flag: 1,
ie: 'utf-8',
sem: 1,
aggr: 0,
perpage: this.requestParams.pageSize,
n: this.requestParams.pageSize,
p: this.requestParams.page,
remoteplace: 'txt.mqq.all',
_: 1459991037831,
jsonpCallback: 'jsonp4'
})
}
},
infiniteHandler ($state) {
if (!this.infiniteLoading) {
this.infiniteLoading = $state
}
this.getResult(true)
},
addSong () {
this.$validator.validateAll().then(result => {
let getErrors = this.vErrors.all()
if (getErrors.length > 0) {
this.$vux.toast.show({
text: getErrors[0],
width: '10em'
})
} else {
let params = Object.assign(this.manualForm, {
ht_id: this.$route.query.id
})
manualAddSong(params).then((res) => {
this.$vux.toast.show('')
this.customVisible = false
})
}
})
}
}
}



